export default {
	data() {
		return {
			list: [],
			loading: "加载中",
			pageNum: 1,
			pageSize: 3,
			total: 0,
			isLoad: false,
			currentLon: 0,
			currentLat: 0
		}
	},
	// 上拉加载
	onReachBottom() {
		let _this = this;
		_this.pageNum++;
		if (_this.pageNum > Math.ceil(_this.total / _this.pageSize)) {
			return _this.loading = '暂无数据';
		}
		if (_this.isLoad) return false;
		_this.isLoad = true;
		_this.request({
			url: _this.http.getRobOrderList,
			data: {
				pageNum: _this.pageNum,
				pageSize: _this.pageSize
			},
			success: (res) => {
				if (res.code === '0') {
					res.result.map((e) => {
						e.currentDistance = _this.GetDistance(_this.currentLat, _this.currentLon, e.startLat, e.startLong);
						_this.list.push(e);
					});
					_this.total = res.total;
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
				_this.loading = '加载完成';
				_this.isLoad = false;
			}
		}, false);
	},
	// 下拉刷新
	onPullDownRefresh() {
		let _this = this;
		_this.pageNum = 1;
		uni.getLocation({
			type: 'wgs84',
			success: function(res) {
				_this.currentLon = res.longitude;
				_this.currentLat = res.latitude;
				_this.request({
					url: _this.http.getRobOrderList,
					data: {
						pageNum: _this.pageNum,
						pageSize: _this.pageSize
					},
					success: (res) => {
						if (res.code === '0') {
							res.result.map((e) => {
								e.currentDistance = _this.GetDistance(_this.currentLat, _this.currentLon, e.startLat, e.startLong);
							});
							_this.list = res.result;
							_this.total = res.total;
						} else {
							uni.showToast({
								title: res.message,
								icon: 'none'
							});
						}
						uni.stopPullDownRefresh();
					}
				}, false);
			}
		});
	},
	onLoad() {
		let _this = this;
		uni.getLocation({
			type: 'wgs84',
			success: function(res) {
				_this.currentLon = res.longitude;
				_this.currentLat = res.latitude;
				_this.init();
			}
		});
	},
	methods: {
		init() {
			let _this = this;
			_this.request({
				url: _this.http.getRobOrderList,
				data: {
					pageNum: _this.pageNum,
					pageSize: _this.pageSize
				},
				success: (res) => {
					let _title = "";
					if (res.code === '0') {
						res.result.map((e) => {
							e.currentDistance = _this.GetDistance(_this.currentLat, _this.currentLon, e.startLat, e.startLong);
						});
						_this.list = res.result;
						_this.total = res.total;
						_title = res.total > 0 ? "加载完成" : "暂无数据";
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
					_this.loading = _title;
					_this.isLoad = false;
				}
			}, false);
		},
		// 计算两点之间的距离
		GetDistance(lat1, lng1, lat2, lng2) {
			var radLat1 = lat1 * Math.PI / 180.0;
			var radLat2 = lat2 * Math.PI / 180.0;
			var a = radLat1 - radLat2;
			var b = lng1 * Math.PI / 180.0 - lng2 * Math.PI / 180.0;
			var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(a / 2), 2) +
				Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)));
			s = s * 6378.137; // EARTH_RADIUS;
			s = Math.round(s * 10000) / 10000;
			return s;
		}

	}
}
